import { useState } from "react";
import { Box, Typography, Tabs, Tab, IconButton } from "@mui/material";
import { HighlightOff } from "@mui/icons-material";
import classes from "./style.module.scss";

const questions = [
	{
		title: "About application",
		answer:
			"It's a news application with a Conversational AI assistant, where the user can get the most recent headlines in mainstream media",
	},
	{
		title: "About Alan AI",
		answer:
			"Alan AI is a complete Conversational AI platform to build, deploy and manage in-app voice assistants in a few days.",
	},
	{
		title: "Get News",
		answer:
			"1) To get the latest news, click the Alan voice button and say 'Give me the latest news' or 'show me the recent headlines'. \n\n 2) To get news by category, click the Alan voice button and say 'give me the latest (category name) news' or 'what are the recent (category name) news?'. \n Categories: business, entertainment, general, health, science, sports, technology. \n\n 3) To get news by term, click the Alan voice button and say 'what is up with (term)' or 'what about the (term)' \n\n 4) To get news from specific source, click the Alan button and say 'give me the news from (source name) \n Ex: BBC, CNN, ABC News..etc' ",
	},
	{
		title: "Read headlines",
		answer:
			"Once retrieving the requested news, Alan will ask you whether you want him to read the titles and you can answer with 'yes' or 'no'.",
	},
	{
		title: "other commands",
		answer:
			"1) To open an original article, Say 'open article number (number)'. \n\n 2) To go back, say 'back'. 3) Free talk: you can make a casual converstaion with alan and he will do his best to give you suitable responses",
	},
	{
		title: "Free talk",
		answer:
			"You can make a casual converstaion with Alan and he will do his best to give you suitable responses",
	},
	{
		title: "Why Alan",
		answer:
			"1) Augment the UX with voice, offering a multi-modal experience. \n\n 2) Users can get real-time, contextual help by asking 'how do I?'. \n\n 3) Increase productivity and adherence to safety and hygiene standards. \n\n 4) Learns your business' unique terminology and workflows. \n\n 5) Iteratively improve the voice UX with real-time visibility of user behaviors. \n\n",
	},
];

interface DocsProps {
	setOpenDocs: React.Dispatch<React.SetStateAction<boolean>>;
}

const Docs: React.FC<DocsProps> = ({ setOpenDocs }) => {
	const [value, setValue] = useState(0);

	const handleChange = (event: React.SyntheticEvent, newValue: number) => {
		setValue(newValue);
	};

	return (
		<Box className={classes.docs}>
			<IconButton onClick={() => setOpenDocs(false)}>
				<HighlightOff />
			</IconButton>
			<Tabs
				orientation="vertical"
				variant="scrollable"
				value={value}
				onChange={handleChange}
			>
				{questions.map((question, index) => (
					<Tab key={index} label={question.title} />
				))}
			</Tabs>

			<Box className="content">
				<Typography>{questions[value].answer}</Typography>
			</Box>
		</Box>
	);
};

export default Docs;
