import { useState } from "react";
import { Box, Typography, Button, IconButton, Modal } from "@mui/material";
import { ArrowCircleDown } from "@mui/icons-material";
import Navbar from "../Navbar";
import Docs from "../Docs";
import classes from "./style.module.scss";

interface CoverProps {
	guideRef: React.MutableRefObject<HTMLElement | null>;
}

const Cover: React.FC<CoverProps> = ({ guideRef }) => {
	const [openDocs, setOpenDocs] = useState(false);

	const handleScroll = () => {
		if (guideRef.current) {
			window.scrollTo(0, guideRef.current.offsetTop);
		}
	};
	return (
		<Box className={classes.cover}>
			<Navbar />
			<video src="assets/cover.mp4" className="video" autoPlay muted loop />
			<Box className="overlay" />

			<Box className="content">
				<Typography variant="h5" className="text">
					Alan-News is an application that uses a Conversational AI
					Assistant to locate articles and breaking news headlines from
					multiple sources across the web
				</Typography>

				<Box className="actions">
					<Button
						variant="contained"
						className="gradiant-btn"
						onClick={handleScroll}
						sx={{ mb: 2 }}
					>
						Get Started
					</Button>
					<Button variant="outlined" onClick={() => setOpenDocs(true)}>
						Docs
					</Button>
				</Box>
			</Box>

			<IconButton className={classes.scrollBtn} onClick={handleScroll}>
				<ArrowCircleDown fontSize="large" sx={{ color: "white" }} />
			</IconButton>

			<Modal
				open={openDocs}
				onClose={() => setOpenDocs(false)}
				className={classes.docsModal}
			>
				<Docs setOpenDocs={setOpenDocs} />
			</Modal>
		</Box>
	);
};

export default Cover;
