import { useRef, useEffect } from "react";
import {
	Box,
	Grid,
	Card,
	CardContent,
	CardMedia,
	CardActions,
	Typography,
	Button,
} from "@mui/material";
import classes from "./style.module.scss";

export interface IArticle {
	source: { id: string; name: string };
	author: string;
	title: string;
	description: string;
	url: string;
	urlToImage: string;
	publishedAt: string;
	content: string;
}

interface IArticleCardProps {
	article: IArticle;
	index: number;
	active: boolean;
}

const ArticleCard: React.FC<IArticleCardProps> = ({
	article,
	index,
	active,
}) => {
	const ref = useRef<HTMLDivElement | null>(null);

	useEffect(() => {
		if (active && ref.current) {
			window.scrollTo(0, ref.current?.offsetTop - 30);
		}
	}, [active]);

	return (
		<Grid item xs={12} sm={6} md={4}>
			<Card raised className={classes.article} ref={ref}>
				<CardMedia
					component="img"
					image={article.urlToImage || "assets/news-placeholder.png"}
					alt="Article"
				/>
				<CardContent>
					<Box display="flex" justifyContent="space-between" mb={2}>
						<Typography variant="caption">
							{new Date(article.publishedAt).toDateString()}
						</Typography>
						<Typography variant="caption">
							{article.source.name}
						</Typography>
					</Box>

					<Typography variant="body1" sx={{ fontWeight: 600 }} mb={2}>
						{article.title}
					</Typography>

					<Typography variant="body2">{article.description}</Typography>
				</CardContent>
				<CardActions>
					<a href={article.url} target="_blank" rel="noreferrer">
						<Button>Read More..</Button>
					</a>
					<Typography>{index + 1}</Typography>
				</CardActions>
				{active ? <Box className="border" /> : null}
			</Card>
		</Grid>
	);
};

export default ArticleCard;
