import React from "react";
import {
	Box,
	Typography,
	Container,
	Card,
	CardContent,
	Grid,
} from "@mui/material";
import classes from "./style.module.scss";

const guides = [
	{
		title: "Latest News",
		name: "",
		examples: "",
		say: "Give me the latest news",
	},
	{
		title: "News by Categories",
		name: "Categories:",
		examples:
			"Business, Entertainment, General, Health, Science, Sports, Technology",
		say: "Give me the latest Technology news",
	},
	{
		title: "News by Terms",
		name: "Terms:",
		examples: "Donald Trump, BitCoin, PlayStation 5, Smartphones...",
		say: "What's up with BitCoin",
	},
	{
		title: "News by Sources",
		name: "Sources:",
		examples: "ABC News, Wired, BBC, Time, IGN, Buzzfeed, CNN...",
		say: "Give me the news from CNN",
	},
];

const Guide = React.forwardRef((_, ref) => {
	return (
		<Box className={classes.guide} ref={ref}>
			<Container>
				<Box className="header">
					<Typography variant="h4" gutterBottom>
						How To Use
					</Typography>
					<Box className="border" />
				</Box>
				<Grid container spacing={3}>
					{guides.map((guide, i) => (
						<Grid key={i} item xs={6} sm={4} md={3} lg={3}>
							<Card raised>
								<CardContent>
									<Typography variant="subtitle1">
										{guide.title}
									</Typography>
									<Box sx={{ textAlign: "center" }}>
										<Typography variant="subtitle1">
											{guide.name}
										</Typography>
										<Typography variant="body2">
											{guide.examples}
										</Typography>
									</Box>
									<Box sx={{ textAlign: "center" }}>
										<Typography variant="body2">
											Try saying:
										</Typography>
										<Typography variant="body2">
											{guide.say}
										</Typography>
									</Box>
								</CardContent>
								<Box className="border" />
							</Card>
						</Grid>
					))}
				</Grid>
			</Container>
		</Box>
	);
});

export default Guide;
