import { useEffect, useRef } from "react";
import { Container, Grid, Box, IconButton } from "@mui/material";
import { ArrowBack } from "@mui/icons-material";
import ArticleCard, { IArticle } from "../ArticleCard";

interface IArticlesListProps {
	articles: IArticle[];
	activeArticle: number;
	setArticles: React.Dispatch<React.SetStateAction<IArticle[]>>;
}

const ArticlesList: React.FC<IArticlesListProps> = ({
	articles,
	activeArticle,
	setArticles,
}) => {
	const eleRef = useRef<HTMLDivElement | null>(null);

	useEffect(() => {
		if (eleRef.current) {
			window.scrollTo(0, eleRef.current.offsetTop);
		}
	}, []);

	return (
		<Box p={3} ref={eleRef}>
			<Container>
				<IconButton sx={{ mb: 2 }} onClick={() => setArticles([])}>
					<ArrowBack />
				</IconButton>
				<Grid container spacing={3}>
					{articles.map((article, index) => (
						<ArticleCard
							key={index}
							article={article}
							index={index}
							active={index === activeArticle}
						/>
					))}
				</Grid>
			</Container>
		</Box>
	);
};

export default ArticlesList;
