import { useState } from "react";
import wordsToNumbers from "words-to-numbers";
import { IArticle } from "../components/ArticleCard";

interface ICommand {
	[key: string]: (data: any) => void;
}

export default function useAlan() {
	const [loading, setLoading] = useState(false);
	const [articles, setArticles] = useState<IArticle[]>([]);
	const [activeArticle, setActiveArticle] = useState(-1);

	const commands: ICommand = {
		fetchNews: (data) => setArticles(data),
		openArticle: (data) => {
			const num = wordsToNumbers(data.number);
			window.open(data.articles[(num as number) - 1].url, "_blank");
		},
		activeArticle: (data) => setActiveArticle(data),
		toggleLoad: () => setLoading((prevLoad) => !prevLoad),
	};

	return {
		articles,
		activeArticle,
		setArticles,
		setActiveArticle,
		loading,
		commands,
	};
}
